.dynamic-form {

  .dynamic-form-initial-animation {
    .react-grid-item.cssTransforms {
      transition-property: transform;
    }
  }
  
  .react-grid-item {
    &.cssTransforms {
      transition-property: none;
    }

    .next-form-item {
      margin-bottom: 0px;
    }
  }

  .animated .react-grid-item.cssTransforms {
    transition-property: transform;
  }

  .dynamic-react-grid-item {
    &:empty {
      margin-top: initial !important;
      width: 0 !important;
      display: initial !important;
      margin-left: 0px !important;
      margin-bottom: initial !important;
    }
    .drag-handlebar, .drag-grid-area-handlebar {
      position: absolute;
      top: -10px;
      right: -10px;
      cursor: move;
      display: none;
    }

    .dynamic-item-tools-button {
      position: absolute;
      top: -4px;
      right: 30px;
      display: none;
    }

    .next-form-item.has-error {
      .next-form-item-help {
        margin-top: 0;
        line-height: 1.2;
      }
    }
  }

  .next-left {
    .next-form-item-control {
      display: inline-block;
    }
  }

  .dynamic-form-grid-layout {
    min-height: 30px;
  }

  &.dynamic-form-editable {
    .dynamic-form-grid-layout {
      background-color: rgba(0, 0, 0, 0.1);
    }

    .dynamic-react-grid-selected-item {
      box-shadow: 0 0 0 4px lightblue;
    }

    .dynamic-react-grid-item {
      position: relative;
      background-color: rgba(255, 255, 255, 0.55);

      &:hover {
        & > .dynamic-item-tools {
          .drag-handlebar, .drag-grid-area-handlebar {
            display: initial;
          }
    
          .dynamic-item-tools-button {
            display: initial;
          }
        }
      }
    }
  }

  .drag-component-box {
    position: fixed;
    // left: 60px;
    top: 30vh;
    right: 0;
    z-index: 1;

    .drag-component-bar {
      cursor: move;
      background-color: #eee;
      padding: 4px;
    }
  }

  .next-form-item {
    display: flex;

    .next-form-item-label {
      white-space: pre;
    }

    .next-form-item-control {
      flex-grow: 1;
    }
  }

  .next-checkbox-group {
    .next-checkbox-wrapper {
      margin: 0 12px 0 0;
      display: inline-block;
      white-space: nowrap;
    }
  }

  .next-radio-group {
    .next-radio-wrapper {
      margin: 0 12px 0 0;
      display: inline-block;
      white-space: nowrap;
    }
  }

  .next-menu {
    height: 300px;
    overflow-y: auto;
  }

  .dynamic-form-text-form-item {
    color: grey;
    line-height: 28px;
  }

  .dynamic-form-text-content {
    white-space: pre-wrap;
  }

  .dynamic-form-item-unit {
    display: inline-block;
  }

  .next-form-item-label {
    label {
      white-space: pre;
    }
  }

  .display-inline-block {
    display: inline-block;
  }

  // 必填的星号
  .form-item-with-custom-label {
    > .next-form-item-label > label[required] {
      &:before {
        margin-right: initial;
        content: initial;
        color: initial;
      }

      .custom-label-wrapper {
        &:before {
          margin-right: 4px;
          content: '*';
          color: #E61C1C;
        }
      }
    }
  }
}

.react-draggable-transparent-selection {
  .dynamic-form {
    .react-grid-item.cssTransforms {
      transition-property: transform;
    }
  }
}
// 用于定位到页面右侧
.dynamic-form-config-popup {
  position: fixed !important;
  top: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  left: initial !important;
  background: white;
}
